import mv from '@/components/mv/index.vue'
import myVideos from "../../components/myvideo/index.vue"
export default {
	data() {
		return {
			alltype:['内地','港台','欧美','日本','韩国'],
			src:'',
			allmv:[],
			personalized:{},
			first:{},
			top:{},
			offset:0,
			flag:0,
			before:true
		}
	},
	components:{
		mv,
		'my-videos':myVideos
	},
	onLoad() {
		this.getMvPersonalized()
		this.getMvFirst()
		this.getMvTop()
		this.getMvAll()
	},
	onReachBottom() {
		if(this.before){
			this.getMvAll()
		}
	},
	methods: {
		error(){
			uni.showModal({
				content: '视频加载错误',
				showCancel: false
			})
		},
		getmv(id){
			uni.navigateTo({
				url: `../mvdetails/mvdetails?id=`+id
			})
		},
		navMvType(){
			uni.navigateTo({
				url: "../mvtype/mvtype"
			})
		},
		navMvTop(){
			uni.navigateTo({
				url: "../mvtop/mvtop"
			})
		},
		getMvPersonalized(){
			this.$request({
				url:'/personalized/mv'
			}).then((data)=>{
				if(data.data.code == 200){
					let personalized = data.data.result
					personalized = personalized.map((item)=>{
						return {
							...item,
							cover:item.picUrl
						}
					})
					this.personalized = personalized
					console.log(personalized,'推荐')
				}
			})
		},
		getMvFirst(){
			this.$request({
				url:'/mv/first?limit=4'
			}).then((data)=>{
				if(data.data.code == 200){
					this.first = data.data.data
					console.log(this.first,'最新mv')
				}
			})
		},
		getMvTop(){
			this.$request({
				url:'/top/mv?limit=2'
			}).then((data)=>{
				if(data.data.code == 200){
					this.top = data.data.data
					console.log(this.top ,'最新mv')
				}
			})
		},
		getMvAll(area){
			let url = ''
			if(area){
				url = '/mv/all?area='+area+'&limit=5&offset='+this.offset
			}else{
				url = '/mv/all?&limit=5&offset='+this.offset
			}
			this.$request({
				url:url
			}).then((data)=>{
				if(data.data.code == 200){
					let allmv = data.data.data
					if(allmv.length < 5){
						this.before = false
					}
					this.allmv.push(...allmv)
					this.offset = this.offset+5
					console.log(this.allmv ,'全部',url)
				}
			})
		},
		videoPlay(id){
			this.$request({
				url:"/mv/url?id="+id
			}).then((data)=>{
				if(data.data.code == 200){
					this.src = data.data.data.url
					this.flag = id
					console.log(this.src)
				}
			})
		},
	}
}
